.Toolbar {
    height: 56px;
    width: 100%;
    position: fixed;
    top:0;
    left: 0;
    background-color: rgb(9, 11, 112);
    display: flex;
    justify-content:space-between ;
    align-items: center;
    padding: 0 20px;
    z-index: 90;
    box-sizing: border-box;
}
.Toolbar nav{
    height: 100%;
}
.Logo{
    height: 80%;  
}
@media(max-width:499px){
    .DesktopOnly{
        display: none;
    }
}